import React from 'react';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { Collapse } from '@material-ui/core';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import { Map } from 'react-lodash';
import SnoozeIcon from '@material-ui/icons/Snooze';
import Button from '@material-ui/core/Button';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Dialog from '@material-ui/core/Dialog';
import RadioGroup from '@material-ui/core/RadioGroup';
import Radio from '@material-ui/core/Radio';
import FormControlLabel from '@material-ui/core/FormControlLabel';

const optionLabels = {
  '1': '1 hour',
  '2': '2 hours',
  '4': '4 hours',
  '24': '24 hours'
}
const options = ['1', '2', '4', '24'];

export default function NotificationEventsGroup({ eventsList, handleClickEvents, snoozeFunction }) {

  const [open, setOpen] = React.useState(false);

  const handleClick = () => {
    setOpen(!open);
  };

  const epochToTime = (epoch) => {
    const d = new Date(0);
    d.setUTCSeconds(epoch / 1000);
    return d.toLocaleTimeString();
  }

  const valueProp = '1';
  const [value, setValue] = React.useState(valueProp);
  const radioGroupRef = React.useRef(null);
  const [openSnooze, setOpenSnooze] = React.useState(false);

  React.useEffect(() => {
    if (!openSnooze) {
      setValue(valueProp);
    }
  }, [valueProp, openSnooze]);

  const handleSnooze = () => {
    setOpenSnooze(true);
  }

  const handleEntering = () => {
    if (radioGroupRef.current != null) {
      radioGroupRef.current.focus();
    }
  };

  const handleCancel = () => {
    setOpenSnooze(false);
  };

  const handleOk = () => {
    setOpenSnooze(false);
    snoozeFunction(eventsList[0].sensorId, value);
  };

  const handleChange = (event) => {
    setValue(event.target.value);
  };

  return (
    <React.Fragment>
      <ListItem>
        <ListItemText primary={eventsList[0].name} secondary={`${eventsList.length} entries`} />
        <ListItemText primary={epochToTime([...eventsList].reverse()[0].timestamp)} />
        <SnoozeIcon onClick={handleSnooze} />
        {open ? <ExpandLess onClick={handleClick} /> : <ExpandMore onClick={handleClick} />}
      </ListItem>
      <Dialog
        disableBackdropClick
        disableEscapeKeyDown
        maxWidth="xs"
        onEntering={handleEntering}
        aria-labelledby="confirmation-dialog-title"
        open={openSnooze}
      >
        <DialogTitle id="confirmation-dialog-title">Snooze For</DialogTitle>
        <DialogContent dividers>
          <RadioGroup
            ref={radioGroupRef}
            aria-label="snooze-for"
            name="snooze-for"
            value={value}
            onChange={handleChange}
          >
            {options.map((option) => (
              <FormControlLabel value={option} key={option} control={<Radio />} label={optionLabels[option]} />
            ))}
          </RadioGroup>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleCancel} color="primary">
            Cancel
        </Button>
          <Button onClick={handleOk} color="primary">
            Ok
        </Button>
        </DialogActions>
      </Dialog>
      <Collapse in={open} timeout="auto" unmountOnExit>
        <Map collection={eventsList} iteratee={entry =>
          <ListItem button onClick={handleClickEvents} key={`${entry.sensorId}-${entry.timestamp}`}>
            <ListItemText inset secondary={epochToTime(entry.timestamp)} primary={entry.message} />
          </ListItem>
        } />
      </Collapse>
    </React.Fragment>
  )
}